@font-face {
    font-family: Josefin;
    src: url('JosefinSans-VariableFont_wght.ttf');
}

* {
    box-sizing: border-box;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #181619;
}

body {
    position: fixed;
    overflow: hidden;
    overscroll-behavior-y: none;
    background-color: #181619;
    cursor: crosshair;
    font-family: Josefin;
}

p {
    margin: 0;
    padding: 0;
}

a {
    padding-right: 10px;
    cursor: pointer;
    pointer-events: all;
    color: black;
    text-decoration: none;
    /* no underline */
}

h1 {
    color: #7015ad;
    position: absolute;
    top: 1%;
    left: 1%;
    text-shadow: 0px 0px 3px #7015ad;
    font-size: 6vw;
}

h3 {
    color: #7015ad;
    text-shadow: 0px 0px 3px #7015ad;
    position: absolute;
    top: 70%;
    right: 30%;
    font-size: 6vw;
    cursor: pointer;
}

:hover {
    color: aqua;
    text-shadow: 0px 0px 7px aqua;
}

h2 {
    color: #7015ad;
    text-shadow: 0px 0px 3px #7015ad;
    position: absolute;
    top: 40%;
    right: 10%;
    font-size: 10vw;
    cursor: pointer;
}

@media screen and (max-width: 568px) {
    .full {
        visibility: hidden;
        display: none;
    }
}